
'use client';

import { useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { PlusCircle, Megaphone, CalendarClock, Info } from 'lucide-react';
import { format, isPast, parseISO } from 'date-fns';
import { Badge } from '@/components/ui/badge';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { useToast } from "@/hooks/use-toast";

interface Notice {
  id: string;
  title: string;
  content: string;
  issuedDate: Date;
  expiryDate?: Date;
  author: string;
}

const noticeFormSchema = z.object({
  title: z.string().min(5, { message: "Title must be at least 5 characters." }),
  content: z.string().min(10, { message: "Content must be at least 10 characters." }),
  author: z.string().min(2, { message: "Author name must be at least 2 characters." }),
  expiryDate: z.string().optional().refine(val => !val || !isNaN(Date.parse(val)), {
    message: "Invalid date format for expiry date.",
  }),
});

type NoticeFormValues = z.infer<typeof noticeFormSchema>;

const initialNotices: Notice[] = [
  { id: '1', title: 'Upcoming Parent-Teacher Meeting', content: 'A parent-teacher meeting is scheduled for next Saturday, 10:00 AM - 01:00 PM. Please register your preferred time slot.', issuedDate: new Date('2024-07-15'), expiryDate: new Date('2024-07-25'), author: 'Admin Office' },
  { id: '2', title: 'Holiday Announcement: Summer Break', content: 'The school will be closed for summer break from July 28th to August 15th. Enjoy your holidays!', issuedDate: new Date('2024-07-10'), author: 'Admin Office' },
  { id: '3', title: 'Library Books Due', content: 'All library books issued before June 1st must be returned by July 20th to avoid fines.', issuedDate: new Date('2024-07-05'), expiryDate: new Date('2024-07-20'), author: 'Librarian' },
  { id: '4', title: 'Annual Science Fair Registration', content: 'Registrations for the Annual Science Fair are now open. Last date to submit projects is August 5th.', issuedDate: new Date('2024-06-20'), expiryDate: new Date('2024-08-05'), author: 'Science Club' },
  { id: '5', title: 'PTA Meeting (Expired)', content: 'PTA meeting was held last month.', issuedDate: new Date('2024-05-01'), expiryDate: new Date('2024-05-15'), author: 'PTA Committee' },
];

export default function NoticesPage() {
  const { user } = useAuth();
  const { toast } = useToast();
  const [notices, setNotices] = useState<Notice[]>(initialNotices);
  const [showCreateModal, setShowCreateModal] = useState(false);

  const form = useForm<NoticeFormValues>({
    resolver: zodResolver(noticeFormSchema),
    defaultValues: {
      title: "",
      content: "",
      author: user?.name || "Admin Office",
      expiryDate: "",
    },
  });

  const handleCreateNotice = (values: NoticeFormValues) => {
    const newNotice: Notice = {
      id: Date.now().toString(),
      title: values.title,
      content: values.content,
      author: values.author,
      issuedDate: new Date(),
      expiryDate: values.expiryDate ? parseISO(values.expiryDate) : undefined,
    };
    setNotices(prevNotices => [newNotice, ...prevNotices].sort((a,b) => b.issuedDate.getTime() - a.issuedDate.getTime()));
    toast({
      title: "Notice Created",
      description: `"${values.title}" has been successfully published.`,
    });
    setShowCreateModal(false);
    form.reset({ author: user?.name || "Admin Office", title: "", content: "", expiryDate: "" });
  };

  const activeNotices = notices.filter(notice => !notice.expiryDate || !isPast(notice.expiryDate)).sort((a,b) => b.issuedDate.getTime() - a.issuedDate.getTime());
  const expiredNotices = notices.filter(notice => notice.expiryDate && isPast(notice.expiryDate)).sort((a,b) => b.issuedDate.getTime() - a.issuedDate.getTime());

  return (
    <div className="space-y-8">
      <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4">
        <div>
          <h1 className="text-3xl font-bold font-headline text-primary">School Notices</h1>
          <p className="text-muted-foreground">Stay informed with the latest announcements.</p>
        </div>
        {user?.role === 'admin' && (
          <Dialog open={showCreateModal} onOpenChange={(isOpen) => {
            setShowCreateModal(isOpen);
            if(!isOpen) form.reset({ author: user?.name || "Admin Office", title: "", content: "", expiryDate: "" });
          }}>
            <DialogTrigger asChild>
              <Button onClick={() => {
                form.reset({ author: user.name, title: "", content: "", expiryDate: "" }); // Ensure reset with current admin name
                setShowCreateModal(true);
              }} className="w-full sm:w-auto">
                <PlusCircle className="mr-2 h-4 w-4" /> Create Notice
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-lg">
              <DialogHeader>
                <DialogTitle className="font-headline">Create New Notice</DialogTitle>
                <DialogDescription>
                  Compose and publish a new school notice.
                </DialogDescription>
              </DialogHeader>
              <Form {...form}>
                <form onSubmit={form.handleSubmit(handleCreateNotice)} className="space-y-4 py-4">
                  <FormField
                    control={form.control}
                    name="title"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Title</FormLabel>
                        <FormControl>
                          <Input placeholder="Enter notice title" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name="content"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Content</FormLabel>
                        <FormControl>
                          <Textarea placeholder="Enter notice details" rows={5} {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                    <FormField
                      control={form.control}
                      name="author"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Author</FormLabel>
                          <FormControl>
                            <Input placeholder="e.g., Admin Office" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    <FormField
                      control={form.control}
                      name="expiryDate"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Expiry Date (Optional)</FormLabel>
                          <FormControl>
                            <Input type="date" {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                  <DialogFooter className="flex-col-reverse sm:flex-row sm:justify-end sm:space-x-2 pt-4">
                    <Button type="button" variant="outline" onClick={() => setShowCreateModal(false)} className="w-full sm:w-auto">Cancel</Button>
                    <Button type="submit" className="w-full sm:w-auto">Publish Notice</Button>
                  </DialogFooter>
                </form>
              </Form>
            </DialogContent>
          </Dialog>
        )}
      </div>

      {notices.length === 0 ? (
         <Card className="text-center py-12">
          <CardHeader>
            <Megaphone className="mx-auto h-16 w-16 text-muted-foreground/50" />
            <CardTitle className="font-headline mt-4">No Notices Yet</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground">There are no active notices at the moment. {user?.role === 'admin' ? 'Try creating one!' : ''}</p>
          </CardContent>
        </Card>
      ) : (
        <>
          {activeNotices.length > 0 && (
            <section>
              <h2 className="text-2xl font-semibold font-headline mb-4 text-primary/90">Active Notices</h2>
              <div className="space-y-6">
                {activeNotices.map((notice) => (
                  <Card key={notice.id} className="shadow-lg hover:shadow-xl transition-shadow duration-300">
                    <CardHeader>
                      <div className="flex justify-between items-start">
                        <CardTitle className="font-headline text-xl mb-1">{notice.title}</CardTitle>
                        <Badge variant="default">Active</Badge>
                      </div>
                      <CardDescription className="text-xs text-muted-foreground">
                        Issued by: {notice.author} on {format(notice.issuedDate, 'PPP')}
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                      <p className="text-sm leading-relaxed">{notice.content}</p>
                    </CardContent>
                    <CardFooter className="text-xs text-muted-foreground">
                      {notice.expiryDate && (
                        <p className="flex items-center">
                          <CalendarClock className="mr-2 h-4 w-4" />
                          Expires on: {format(notice.expiryDate, 'PPP')}
                        </p>
                      )}
                    </CardFooter>
                  </Card>
                ))}
              </div>
            </section>
          )}

          {expiredNotices.length > 0 && (
            <section className="mt-12">
              <h2 className="text-2xl font-semibold font-headline mb-4 text-muted-foreground">Expired Notices</h2>
              <div className="space-y-4">
                {expiredNotices.map((notice) => (
                  <Card key={notice.id} className="opacity-70 bg-muted/30">
                    <CardHeader>
                       <div className="flex justify-between items-start">
                        <CardTitle className="font-headline text-lg mb-1">{notice.title}</CardTitle>
                        <Badge variant="secondary">Expired</Badge>
                      </div>
                      <CardDescription className="text-xs text-muted-foreground">
                        Issued by: {notice.author} on {format(notice.issuedDate, 'PPP')}
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                      <p className="text-sm text-muted-foreground line-clamp-3">{notice.content}</p>
                    </CardContent>
                     <CardFooter className="text-xs text-muted-foreground">
                        {notice.expiryDate && (
                            <p className="flex items-center">
                            <Info className="mr-2 h-4 w-4" />
                            Expired on: {format(notice.expiryDate, 'PPP')}
                            </p>
                        )}
                    </CardFooter>
                  </Card>
                ))}
              </div>
            </section>
          )}
        </>
      )}
    </div>
  );
}
